<template>
    <div>
        <div class="card mt-5" v-for="(nws,i) in news" :key="i">
            <img :src="nws._embedded['wp:featuredmedia'][0].source_url" alt="" class="card-img-top">
            <div class="card-body">
              <p class="m-0">{{new Date(nws.date).toLocaleDateString("id-ID",options_date)}}</p>
              <a :href="nws.link" target="_blank">
                <h5>{{nws.title.rendered}}</h5>
              </a>
            </div>
        </div>
    </div>
</template>

<script>
    import axios from 'axios'
    export default {
        name:'NewsFeed',
        data: () => ({
            BASE_URL:process.env.VUE_APP_URL,
            news:[],
            options_date:{ weekday: 'long', year: 'numeric', month: 'long', day: 'numeric' }
        }),
        created(){
          this.getNews()
        },
        methods:{
          async getNews(){
            let res = await axios.get("https://ikarholaz.com/wp-json/wp/v2/posts?_embed")
            this.news = res.data
          }
        }
    }
</script>

