        <div class="back-button"><router-link to="/profile">
            <svg class="bi bi-arrow-left" xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="currentColor" viewBox="0 0 16 16">
              <path fill-rule="evenodd" d="M15 8a.5.5 0 0 0-.5-.5H2.707l3.147-3.146a.5.5 0 1 0-.708-.708l-4 4a.5.5 0 0 0 0 .708l4 4a.5.5 0 0 0 .708-.708L2.707 8.5H14.5A.5.5 0 0 0 15 8z"></path>
            </svg></router-link></div>
        <!-- Page Title-->
        <div class="page-heading">
          <h6 class="mb-0">Edit Profile</h6>
        </div>
        <div></div>
      </div>
    </div>
    
    <div class="page-content-wrapper">
      <div class="container">
        <!-- Profile Wrapper-->
        <div class="profile-wrapper-area py-3">
          <!-- User Information-->
          <div class="card user-info-card">
            <div class="card-body p-4 d-flex align-items-center">
              <div class="user-profile me-3">

                <img :src="STORAGE_URL+'/public/'+userLoggedIn.alumni.profile_pic" :alt="userLoggedIn.name" v-if="userLoggedIn.alumni.profile_pic" class="profile">
                <img src="../assets/img/bg-img/9.jpg" alt="" v-else>

                <input
                  type="file"
                  style="display: none"
                  ref="image"
                  accept="image/*"
                  @change="onFilePicked">

                <div class="change-user-thumb">
                    <button @click="pickFile"><i class="lni lni-pencil"></i></button>
                </div>

              </div>
              <div class="user-info">
                <p class="mb-0 text-dark">{{userLoggedIn.alumni.email ? userLoggedIn.alumni.email : '-'}}</p>
                <p class="mb-0 text-dark">{{userLoggedIn.alumni.NRA ? userLoggedIn.alumni.NRA : '-'}}</p>
                <h5 class="mb-0">{{userLoggedIn.name}}</h5>
              </div>
            </div>
          </div>

 <!-- Single Setting Card-->
          <div class="card settings-card mt-2">
            <div class="card-body">
              <!-- Single Settings-->
              <div class="single-settings d-flex align-items-center justify-content-between">
                <div class="title private"><i class="lni lni-protection bg-success"></i><span>Private Email</span></div>
                <div class="data-content">
                  <div class="toggle-button-cover">
                    <div class="button r">
                      <input class="checkbox" type="checkbox" v-model="userLoggedIn.alumni.private_email">
                      <div class="knobs"></div>
                      <div class="layer"></div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <!-- Single Setting Card-->
          <div class="card settings-card">
            <div class="card-body">
              <!-- Single Settings-->
              <div class="single-settings d-flex align-items-center justify-content-between">
                <div class="title private"><i class="lni lni-protection bg-success"></i><span>Private No HP</span></div>
                <div class="data-content">
                  <div class="toggle-button-cover">
                    <div class="button r">
                      <input class="checkbox" type="checkbox" v-model="userLoggedIn.alumni.private_phone">
                      <div class="knobs"></div>
                      <div class="layer"></div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <!-- Single Setting Card-->
          <div class="card settings-card">
            <div class="card-body">
              <!-- Single Settings-->
              <div class="single-settings d-flex align-items-center justify-content-between">
                <div class="title private"><i class="lni lni-protection bg-success"></i><span>Private Domisili</span></div>
                <div class="data-content">
                  <div class="toggle-button-cover">
                    <div class="button r">
                      <input class="checkbox" type="checkbox" v-model="userLoggedIn.alumni.private_domisili">
                      <div class="knobs"></div>
                      <div class="layer"></div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <!-- Single Setting Card-->
          <!-- <div class="card settings-card">
            <div class="card-body">
              
              <div class="single-settings d-flex align-items-center justify-content-between">
                <div class="title private"><i class="lni lni-map-marker bg-success"></i><span>Tinggal di luar negeri</span></div>
                <div class="data-content">
                  <div class="toggle-button-cover">
                    <div class="button r">
                      <input class="checkbox" type="checkbox" v-model="isForeign">
                      <div class="knobs"></div>
                      <div class="layer"></div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div> -->

