          <div class="card user-data-card">
            <div class="card-body">
                <div class="mb-3">
                  <div class="title mb-2"><i class="lni lni-user"></i><span>Full Name</span></div>
                  <input class="form-control" type="text" placeholder="Suha Jannat" v-model="userLoggedIn.name">
                </div>
                <div class="mb-3">
                  <div class="title mb-2"><i class="lni lni-user"></i><span>Kelas</span></div>
                  <input class="form-control" type="text" placeholder="Kelas 2" v-model="userLoggedIn.alumni.class_name">
                </div>
                <div class="mb-3">
