                  <input class="form-control" type="text" placeholder="+880 000 111 222" v-model="userLoggedIn.email">
                </div>
                <div class="mb-3">
                  <div class="title mb-2"><i class="lni lni-envelope"></i><span>Email Address</span></div>
                  <input class="form-control" type="email" placeholder="care@example.com" v-model="userLoggedIn.alumni.email">
                </div>
