                                </select>
                            </div>
                            <div class="form-group mb-3">
                                <label for="">Kepemilikan</label>
                                <select v-model="business.kepemilikan" class="form-control">
                                    <option value="">- Pilih Kepemilikan -</option>
                                    <option value="Keluarga">Keluarga</option>
                                    <option value="Pribadi">Pribadi</option>
                                    <option value="Kongsi">Kongsi</option>
                                </select>
                            </div>
                            <div class="form-group mb-3">
                                <label for="">Status kepemilikan</label>
                                <select v-model="business.status_kepemilikan" class="form-control">
                                    <option value="">- Pilih Status Kepemilikan -</option>
                                    <option value="Milik">Milik</option>
                                    <option value="Sewa">Sewa</option>
                                </select>
                            </div>
                            <div class="form-group mb-3">
                                <label for="">Skala</label>
                                <select v-model="business.skala" class="form-control">
                                    <option value="">- Pilih Skala -</option>
                                    <option value="Mikro">Mikro</option>
                                    <option value="Kecil">Kecil</option>
                                    <option value="Menengah">Menengah</option>
                                    <option value="Besar">Besar</option>
                                </select>
                            </div>
                            <div class="form-group mb-3">
                                <label for="">Berdiri Sejak</label>
                                <input class="form-control" type="text" placeholder="Berdiri Sejak" v-model="business.berdiri_sejak">
