                                <select class="form-control mt-2" v-model="business.province" @change="changeProvince2(business.province)">
                                  <option value="">- Pilih Provinsi -</option>
                                  <option v-for="province  in provinces" :key="province.province_id" :value="province.province">{{province.province}}</option>
                                </select>
                            </div>
                            <div class="form-group mb-3">
                                <label for="">Kabupaten / Kota</label>
                                <select class="form-control mt-2" v-model="business.city">
                                  <option value="">- Pilih Kabupaten / Kota -</option>
                                  <option v-if="business.city && ((cities.length && !cities.find(province => province.province == business.city)) || !cities.length)" :value="business.city">{{business.city}}</option>
                                  <option v-for="city  in cities" :key="city.city_id" :value="city.city_name">{{city.city_name}}</option>
                                </select>
                            </div>
                            <div class="form-group mb-3">
                                <label for="">Alamat</label>  
                                <input class="form-control" type="text" placeholder="Alamat" v-model="business.alamat">
                            </div>
