                            </div>
                            <div class="form-group mb-3">
                                <label for="">Website</label>
                                <input class="form-control" type="text" placeholder="Website" v-model="business.website">
                            </div>
                            <div class="form-group mb-3">
                                <label for="">Jumlah SDM / Karyawan</label>
                                <input class="form-control" type="text" placeholder="Jumlah SDM / Karyawan" v-model="business.jumlah_sdm">
                            </div>
                            <div class="form-group mb-3">
                                <label for="">Ijin Usaha</label>
                                <select v-model="business.ijin_usaha" class="form-control">
