                            <div>
                                <button class="btn btn-success mr-2" type="button" @click="collapse('#community-'+i)"><i class="lni lni-arrow-down"></i></button>
                                <button class="btn btn-danger" type="button" @click="removeCommunity(i,community.id)"><i class="lni lni-close"></i></button>
                            </div>
                        </div>
                        <div class="d-none" :id="'community-'+i">
