                                <label for="">Nama</label>
                                <input class="form-control" type="text" placeholder="Nama" v-model="community.name" required>
                            </div>
                            <div class="form-group mb-3">
                                <label for="">Bidang</label>
                                <select v-model="community.bidang" class="form-control">
                                    <option value="">- Pilih Bidang -</option>
                                    <option v-for="(bidang,idx) in communities" :key="idx" :value="bidang">{{bidang}}</option>
                                </select>
                            </div>
                            <div class="form-group mb-3">
