                                <input class="form-control" type="text" placeholder="Pencapaian" v-model="community.pencapaian">
                            </div>
                            <div class="form-group mb-3">
                                <label for="">Keaktifan</label>
                                <select v-model="community.keaktifan" class="form-control">
                                    <option value="">- Pilih Keaktifan -</option>
                                    <option value="Founder">Founder</option>
                                    <option value="Pembina">Pembina</option>
                                    <option value="Pengurus">Pengurus</option>
                                    <option value="Anggota">Anggota</option>
                                </select>
                            </div>
                            <div class="form-group mb-3">
                                <label for="">No Telepon</label>
                                <input class="form-control" type="text" placeholder="No Telepon" v-model="community.no_telepon">
