                          <div>
                              <button class="btn btn-success mr-2" type="button" @click="collapse('#profession-'+i)"><i class="lni lni-arrow-down"></i></button>
                              <button class="btn btn-danger" type="button" @click="removeProfession(i,profession.id)"><i class="lni lni-close"></i></button>
                          </div>
