                              <button class="btn btn-success mr-2" type="button" @click="collapse('#training-'+i)"><i class="lni lni-arrow-down"></i></button>
                              <button class="btn btn-danger" type="button" @click="removeTraining(i,training.id)"><i class="lni lni-close"></i></button>
