                      </div>
                      <div class="d-none" :id="'training-'+i">
                          <div class="form-group mb-3">
                              <label for="">Nama</label>
                              <input class="form-control" type="text" placeholder="Nama" v-model="training.name" required>
                          </div>
                          <div class="form-group mb-3">
                              <label for="">Pemberi pelatihan</label>
                              <input class="form-control" type="text" placeholder="Pemberi pelatihan" v-model="training.pemberi_pelatihan">
                          </div>
                          <div class="form-group mb-3">
                              <label for="">Tahun</label>
                              <input class="form-control" type="text" placeholder="Tahun" v-model="training.tahun">
                          </div>
                          <div class="form-group mb-3">
                              <label for="">Keterangan</label>
                              <input class="form-control" type="text" placeholder="Keterangan" v-model="training.keterangan">
                          </div>
                      </div>
                  </div>

                  </div>
                </div>

                <div class="mb-3">
                  <div class="d-flex justify-content-between">
                    <div class="title mb-2">
                      <i class="lni lni-briefcase"></i><span>Penghargaan</span>
                    </div>
                    <v-btn icon small @click="addAppreciation">
                        <v-icon>mdi-plus</v-icon>
                      </v-btn>
                  </div>
                  <div id="appreciations" v-for="(appreciation,i) in userLoggedIn.alumni.appreciations" :key="i">
                    <div class="card card-body mb-3">
                      <div class="d-flex justify-content-between align-items-center">
                          <p>Penghargaan ke {{i+1}}</p>
                          <div>
                              <button class="btn btn-success mr-2" type="button" @click="collapse('#appreciation-'+i)"><i class="lni lni-arrow-down"></i></button>
                              <button class="btn btn-danger" type="button" @click="removeAppreciation(i,appreciation.id)"><i class="lni lni-close"></i></button>
                          </div>
                      </div>
                      <div class="d-none" :id="'appreciation-'+i">
                          <div class="form-group mb-3">
                              <label for="">Nama</label>
                              <input class="form-control" type="text" placeholder="Nama" v-model="appreciation.name" required>
                          </div>
                          <div class="form-group mb-3">
                              <label for="">Pemberi penghargaan</label>
                              <input class="form-control" type="text" placeholder="Pemberi penghargaan" v-model="appreciation.pemberi_penghargaan">
                          </div>
                          <div class="form-group mb-3">
                              <label for="">Tahun</label>
                              <input class="form-control" type="text" placeholder="Tahun" v-model="appreciation.tahun">
                          </div>
                          <div class="form-group mb-3">
                              <label for="">Keterangan</label>
                              <input class="form-control" type="text" placeholder="Keterangan" v-model="appreciation.keterangan">
                          </div>
                      </div>
                    </div>

                  </div>
                </div>

                <div class="mb-3">
                  <div class="d-flex justify-content-between">
                    <div class="title mb-2">
                      <i class="lni lni-briefcase"></i><span>Minat</span>
                    </div>
                    <v-btn icon small @click="addInterest">
                        <v-icon>mdi-plus</v-icon>
                      </v-btn>
                  </div>
                  <div id="interests" v-for="(interest,i) in userLoggedIn.alumni.interests" :key="i">
                    <div class="card card-body mb-3">
                      <div class="d-flex justify-content-between align-items-center">
                          <p>Minat ke {{i+1}}</p>
                          <div>
                              <button class="btn btn-success mr-2" type="button" @click="collapse('#interest-'+i)"><i class="lni lni-arrow-down"></i></button>
                              <button class="btn btn-danger" type="button" @click="removeInterest(i,interest.id)"><i class="lni lni-close"></i></button>
                          </div>
                      </div>
                      <div class="d-none" :id="'interest-'+i">
                          <div class="form-group mb-3">
                              <label for="">Bidang</label>
                              <input class="form-control" type="text" placeholder="Bidang" v-model="interest.bidang" required>
                          </div>
                          <div class="form-group mb-3">
                              <label for="">Gaji</label>
                              <input class="form-control" type="text" placeholder="Gaji" v-model="interest.gaji" required>
                          </div>
                      </div>
                    </div>

                  </div>
                </div>

                <button class="btn btn-success w-100 text-white" type="submit" @click="editProfile()">Save All Changes</button>
              
            </div>
          </div>

        </div>
      </div>
    </div>
</div>
</template>

<style scoped>
#bg{
  background: #e5e8ff !important;
}
.private{
  font-size:16px !important;
}
</style>

<script>
import axios from 'axios'
export default {
  name: 'EditProfile',
  data: () => ({
    userLoggedIn:JSON.parse(localStorage.getItem("userLoggedIn")),
    saveLoading:false,
    profileLoading:false,
    snackbar:false,
    snackbarText:'',
    BASE_URL:process.env.VUE_APP_URL,
    STORAGE_URL:process.env.VUE_APP_STORAGE_URL,
    isForeign:false,
    provinces:[],
    sektors:[],
    communities:[],
    professions:[],
    badan_hukums:[],
    ijin_usahas:[],
    cities:[],
    selectedProvince: "",
    selectedCity:"",
    center: { lat: -6.200000,  lng: 106.816666 },
    markers:[],
  }),
  created(){
     this.getProvinces()
     this.getFields("sektors")
     this.getFields("communities")
     this.getFields("professions")
     this.getFields("badan_hukums")
     this.getFields("ijin_usahas")
  },
  mounted(){
    this.geolocate()
  },
  computed:{
    shortName(){
      let name = this.userLoggedIn.name

      let names = name.split(' ')

      var stName = ''

      names.forEach(s=>{
        stName += s[0]
      })
      
      return stName
    },
    getProvinceByName(){
      return this.provinces.find(province => province.province == this.selectedProvince)
    },
    places(){
      let user = this.userLoggedIn.alumni
      return user.address && user.city && user.province && user.country ? user.address+", "+user.city+", "+user.province+", "+user.country : '-'
    }
  },
  methods:{
    updateCoordinates(location) {
      this.center = {
          lat: location.latLng.lat(),
          lng: location.latLng.lng(),
      };
      this.$gmapApiPromiseLazy().then(() => {
        const geocoder = new google.maps.Geocoder()
        geocoder.geocode({ location: this.center }).then((response) => {
          if (response.results[0]) {
            // const address = "Jl. Lintas Sumatra No.16, Pulau Rakyat Tua, Kec. Pulau Rakyat, Kabupaten Asahan, Sumatera Utara 21273, Indonesia"
            const formatted_address = response.results[0].formatted_address
            const [address,street,district,regency, province, country] = formatted_address.split(", ")
            this.userLoggedIn.alumni.address = address,", ",street+", "+district
            this.userLoggedIn.alumni.city = regency
            this.userLoggedIn.alumni.province = province
            this.userLoggedIn.alumni.country = country
          } else {
            window.alert("No results found");
          }
        })
      })

    },
    geolocate: function() {
      var ths = this
      this.$gmapApiPromiseLazy().then(() => {
        const geocoder = new google.maps.Geocoder()
        geocoder.geocode({'address': this.places}, function(results, status) {
          if (status == 'OK') {
            ths.center = {
              lat:results[0].geometry.location.lat(),
              lng:results[0].geometry.location.lng(),
            }

            ths.markers.push({ position: ths.center })
          } else {
            alert('Geocode was not successful for the following reason: ' + status);
          }
        });
      })
      
    },
    selectProvince(){
      if(this.userLoggedIn.alumni.province){
        this.selectedProvince = this.userLoggedIn.alumni.province
        this.changeProvince()
      }
    },
    selectCity(){
      if(this.userLoggedIn.alumni.city){
        this.selectedCity = this.userLoggedIn.alumni.city
      }
    },
    async getProvinces(){
      let response = await axios.get(this.BASE_URL+"/api/get-provinces")
      let data = await response.data
      this.provinces = data
      this.selectProvince()
    },
    async getFields(field){
      let response = await axios.get(this.BASE_URL+"/api/get-fields/"+field)
      let data = await response.data
      switch (field) {
        case "sektors":
          this.sektors = data
          break;
      
        case "communities":
          this.communities = data
          break;
      
        case "professions":
          this.professions = data
          break;
      
        case "badan_hukums":
          this.badan_hukums = data
          break;
      
        case "ijin_usahas":
          this.ijin_usahas = data
          break;
      
        default:
          break;
      }
    },
    changeProvince(){
      this.selectedCity = ""
      this.getCities()
    },
    changeProvince2(val){
      this.getCities2(val)
    },
    async getCities(){
      let response = await axios.get(this.BASE_URL+"/api/get-district/"+this.getProvinceByName.province_id)
      let data = await response.data
      this.cities = data
      this.selectCity()
    },
    async getCities2(val){
      let prov = this.provinces.find(province => province.province == val)
      let response = await axios.get(this.BASE_URL+"/api/get-district/"+prov.province_id)
      let data = await response.data
      this.cities = data
      this.selectCity()
    },
    async editProfile(){
      this.saveLoading = true

      try{
        let data = {
          'name' : this.userLoggedIn.name,
          'email' : this.userLoggedIn.alumni.email,
          'phone' : this.userLoggedIn.email,
          'address' : this.userLoggedIn.alumni.address,
          'city' : this.selectedCity,
          'province' : this.selectedProvince,
          'country' : this.userLoggedIn.alumni.country,
          'year_in' : this.userLoggedIn.alumni.year_in,
          'class_name' : this.userLoggedIn.alumni.class_name,
          'graduation_year' : this.userLoggedIn.alumni.graduation_year,
          'date_of_birth' : this.userLoggedIn.alumni.date_of_birth,
          'private_email' : this.userLoggedIn.alumni.private_email,
          'private_phone' : this.userLoggedIn.alumni.private_phone,
          'private_domisili' : this.userLoggedIn.alumni.private_domisili,
          'skills' : this.userLoggedIn.alumni.skills,
          'businesses' : this.userLoggedIn.alumni.businesses,
          'communities' : this.userLoggedIn.alumni.communities,
          'professions' : this.userLoggedIn.alumni.professions,
          'trainings' : this.userLoggedIn.alumni.trainings,
          'appreciations' : this.userLoggedIn.alumni.appreciations,
          'interests' : this.userLoggedIn.alumni.interests,
        }

        let response = await axios.post(this.BASE_URL+"/api/mobile/alumni/edit",data)
        let newData = await response.data

        localStorage.setItem("userLoggedIn",JSON.stringify(newData.data))

        this.snackbarText = newData.message
        this.snackbar = true
        
      }catch{
        this.snackbarText = "Gagal edit profile"
        this.snackbar = true
        
      }

      this.saveLoading = false
    },
    addSkill(){
      this.userLoggedIn.alumni.skills.push({name:''})
    },
    async removeSkill(i,id){
      
      this.userLoggedIn.alumni.skills.splice(i,1)

      if(id){
        await axios.get(this.BASE_URL+"/api/mobile/alumni/delete-skill/"+id)
        localStorage.setItem("userLoggedIn",JSON.stringify(this.userLoggedIn))
      }

    },
    collapse(id){
      let item = document.querySelector(id)
      item.classList.toggle('d-none')
    },
    addBusiness(){
      this.userLoggedIn.alumni.businesses.push({name:'',})
    },
    async removeBusiness(i,id){
      
      this.userLoggedIn.alumni.businesses.splice(i,1)

      if(id){
        await axios.get(this.BASE_URL+"/api/mobile/alumni/delete-business/"+id)
        localStorage.setItem("userLoggedIn",JSON.stringify(this.userLoggedIn))
      }

    },
    addCommunity(){
      this.userLoggedIn.alumni.communities.push({name:'',})
    },
    async removeCommunity(i,id){
      
      this.userLoggedIn.alumni.communities.splice(i,1)

      if(id){
        await axios.get(this.BASE_URL+"/api/mobile/alumni/delete-community/"+id)
        localStorage.setItem("userLoggedIn",JSON.stringify(this.userLoggedIn))
      }

    },
    addProfession(){
      this.userLoggedIn.alumni.professions.push({company_name:'',})
    },
    async removeProfession(i,id){
      
      this.userLoggedIn.alumni.professions.splice(i,1)

      if(id){
        await axios.get(this.BASE_URL+"/api/mobile/alumni/delete-profession/"+id)
        localStorage.setItem("userLoggedIn",JSON.stringify(this.userLoggedIn))
      }

    },
    addTraining(){
      this.userLoggedIn.alumni.trainings.push({name:'',})
    },
    async removeTraining(i,id){
      
      this.userLoggedIn.alumni.trainings.splice(i,1)

      if(id){
        await axios.get(this.BASE_URL+"/api/mobile/alumni/delete-training/"+id)
        localStorage.setItem("userLoggedIn",JSON.stringify(this.userLoggedIn))
      }

    },
    addAppreciation(){
      this.userLoggedIn.alumni.appreciations.push({name:'',})
    },
    async removeAppreciation(i,id){
      
      this.userLoggedIn.alumni.appreciations.splice(i,1)

      if(id){
        await axios.get(this.BASE_URL+"/api/mobile/alumni/delete-appreciation/"+id)
        localStorage.setItem("userLoggedIn",JSON.stringify(this.userLoggedIn))
      }

    },
    addInterest(){
      this.userLoggedIn.alumni.interests.push({bidang:'',})
    },
    async removeInterest(i,id){
      
      this.userLoggedIn.alumni.interests.splice(i,1)

      if(id){
        await axios.get(this.BASE_URL+"/api/mobile/alumni/delete-interest/"+id)
        localStorage.setItem("userLoggedIn",JSON.stringify(this.userLoggedIn))
      }

    },
    pickFile() {
      this.profileLoading = true
      this.$refs.image.click()
    },

    async onFilePicked(e) {
      
      const files = e.target.files
      if (files[0] !== undefined) {
        
        if (files[0].name.lastIndexOf('.') <= 0) {
          return
        }

        let data = new FormData();

        data.append('profile',files[0])
        data.append('phone',this.userLoggedIn.email)

        try{
          let response = await axios.post(this.BASE_URL+"/api/mobile/alumni/upload-profile",data, {
            headers: {
                'Content-Type': 'multipart/form-data'
            }
          })

          let newData = await response.data
          
          this.userLoggedIn.alumni.profile_pic = newData.data.alumni.profile_pic
          
          localStorage.setItem("userLoggedIn",JSON.stringify(newData.data))

          this.snackbarText = newData.message
          this.snackbar = true
        }catch{
          this.snackbarText = "Gagal edit foto profile"
          this.snackbar = true
            
        }
      } 
      
      this.profileLoading = false

    }
  }
}
</script>